<template>
  <div class="cash scroll-list-wrap">
    <cube-scroll
            ref="scroll"
            :data="list"
            :options="options"
            @pulling-down="onPullingDown"
            @pulling-up="onPullingUp">
    <div class="g-swiper">
      <div class="m-list">
        <cube-slide :showDots="showDots" :data="items"/>
      </div>
    </div>
    <div class="g-goodList">
      <router-link v-for="(v,i) in list" :key="i" :to="{name:'detail',query:{id:v.id}}" class="m-box" tag="div">
        <div class="s-img">
          <img v-lazy="'https://fx18.oss-cn-hangzhou.aliyuncs.com/'+v.carousels[0]" alt="">
        </div>
        <div class="s-msg beyond">{{v.proName}}</div>
        <div class="s-ps">
          <span>{{v.price}}元宝</span>
        </div>
      </router-link>
    </div>
    </cube-scroll>
  </div>
</template>

<script>
  export default {
    name: "cash",
    data() {
      return {
        showDots: false,
        items: [],
        list: [],/*商品数据*/
        options: {
          /*滚动配置*/
          scrollbar: {fade: true},
          pullUpLoad: {txt: {noMore: '没有更多数据了'}},
          pullDownRefresh: {stopTime: 0, txt: ' '}
        },
        bit:{
          page:1,
          sortId:3,
        }
      }
    },
    created(){
      this.swiper();//轮播图
      this.goodList();//列表
    },
    methods:{
      swiper(){//轮播
        this.$post(this.$bee+'/api/index/listAdvert',{place:2}).then((data)=>{
          if(data.code==200){
            data.data.adverts.forEach((i)=>{
              this.items.push({url:(i.url?i.url:'javascript:void(0)'),image:'https://fx18.oss-cn-hangzhou.aliyuncs.com/'+i.pic})
            })
          }else{
            this.$createToast({
              txt: data.message,
              type: 'txt'
            }).show();
          }
        })
      },
      goodList(){
        this.$post(this.$bee+'/api/product/listProduct',this.bit).then((data)=> {
          if (data.code == 200&&data.data.pros.length!=0) {
            data.data.pros.forEach((i) => {
              this.list.push(i)
            })
            this.bit.page++;
          } else {
            this.$refs.scroll.forceUpdate()
          }
        })
      },
      onPullingDown() {/*下拉刷新*/
        this.bit.page=1;
        this.list=[];
        this.goodList();
      },
      onPullingUp() {/*上拉加载*/
        this.goodList();
      },
    }
  }
</script>

<style scoped lang="stylus">
  .g-swiper
    .m-list
      width 100%
      & >>> .cube-slide
        width 100%
        img
          width 100%
          height 100%
  .g-goodList
    display flex
    flex-wrap wrap
    padding 7.5px 5px 10px
    .m-box
      box-sizing border-box
      width 172.5px
      overflow hidden
      margin 0 5px
      padding-bottom 12.5px
      box-shadow: 0px 5px 13.5px 1.5px rgba(84, 84, 85, 0.3)
      margin-bottom 10px
      .s-img
        width 100%
        img
          width 100%
          height 165x
      .s-msg
        font-size 13.5px
        padding 5px 5px
        margin-top 7.5px
        text-align left
        color #5c5b5b
      .s-ps
        text-align left
        padding 0 5px
        span
          font-size: 14.5px
          color #c38b30
          &:nth-child(2)
            margin-left 10px
</style>
